import { createElement, useMemo } from 'react';
import type { FC, ReactNode } from 'react';
import { isCliSystemError, normalizeError } from '@wix/cli-error';
import { getErrorComponent } from '../get-error-component.js';

interface Props {
  error: unknown;
  systemErrorOverride?: ReactNode;
}

export const ErrorViewer: FC<Props> = ({
  error: originalError,
  systemErrorOverride,
}) => {
  return useMemo(() => {
    const error = normalizeError(originalError);

    return isCliSystemError(error) && systemErrorOverride
      ? systemErrorOverride
      : createElement(getErrorComponent(error.cliCode));
  }, [originalError, systemErrorOverride]);
};
