import { isType, variant, fields } from 'variant';
import type { VariantOf } from 'variant';
import {
  NodeClient,
  Scope,
  getDefaultIntegrations,
  defaultStackParser,
  makeNodeTransport,
} from '@sentry/node';
import { getTestOverrides } from '@wix/cli-test-overrides';
import { extractHttpError, getHttpErrorInfo } from '@wix/cli-http-client';
import VError from 'verror';
import { outdent } from 'outdent';
import type { CliError } from '@wix/cli-error';
import {
  isCliSystemError,
  normalizeError,
  CliSystemErrorCode,
} from '@wix/cli-error';
import EventEmitter from 'node:events';

export interface TypedEventEmitter {
  on: (event: 'errorReported', listener: (error: CliError) => void) => this;

  emit: (event: 'errorReported', error: CliError) => boolean;
}

export interface ErrorReporterOptions {
  dsn: string;
  release: string;
}

interface User {
  id: string;
}

interface UserFeedback {
  userId: string;
  eventId: string;
  feedback: string;
}

export type ErrorReporter = ReturnType<typeof createErrorReporter>;

export const ReportErrorResult = variant({
  Reported: fields<{ eventId: string }>(),
  Ignored: {},
});
export type ReportErrorResult = VariantOf<typeof ReportErrorResult>;

const reportError = (
  scope: Scope,
  originalError: CliError,
  { data }: { data?: unknown } = {}
) => {
  if (!isCliSystemError(originalError)) {
    return ReportErrorResult.Ignored();
  }

  const error = isType(originalError.cliCode, CliSystemErrorCode.UnknownFailure)
    ? originalError.cliCode.error
    : originalError;

  const eventId = scope.captureException(error, {
    data,
    captureContext: (localScope) => {
      const httpError = extractHttpError(error);
      if (httpError) {
        localScope.setTag('requestId', httpError.requestId);
        localScope.setContext('requestInfo', getHttpErrorInfo(httpError));
      }
      if (error instanceof Error) {
        localScope.setContext('error details', {
          info: VError.info(error),
          fullStack: VError.fullStack(error),
        });
      }

      return localScope;
    },
  });

  return ReportErrorResult.Reported({ eventId });
};

export function createErrorReporter({ dsn, release }: ErrorReporterOptions) {
  const { sentryDsn: sentryDsnOverride } = getTestOverrides();

  // https://docs.sentry.io/platforms/javascript/best-practices/multiple-sentry-instances/
  const sentryClient = new NodeClient({
    enabled:
      sentryDsnOverride !== undefined || process.env.NODE_ENV === 'production',
    dsn: sentryDsnOverride ?? dsn,
    release,
    integrations: getDefaultIntegrations({}).filter(
      (integration) =>
        integration.name !== 'OnUnhandledRejection' &&
        integration.name !== 'OnUncaughtException' &&
        // By default, @sentry/node adds metadata about installed packages.
        integration.name !== 'Modules'
    ),
    stackParser: defaultStackParser,
    transport: makeNodeTransport,
    beforeSend(event) {
      // By default @sentry/node includes the local hostname as `server_name`
      // which is sensitive and not useful.

      delete event.server_name;
      return event;
    },
    // Since we pass `requestInfo`, the default depth of 3
    // redacts most useful info passed from API error responses
    // so we increase it to include more of the potential error http repsonse.
    normalizeDepth: 10,
  });

  const sentryScope = new Scope();
  sentryScope.setClient(sentryClient);
  sentryClient.init();

  const emitter: TypedEventEmitter = new EventEmitter();

  return {
    on: emitter.on.bind(emitter),
    reportError: (
      originalError: unknown,
      { data }: { data?: unknown } = {}
    ) => {
      const error = normalizeError(originalError);
      const result = reportError(sentryScope, error, { data });

      emitter.emit('errorReported', error);

      return result;
    },
    reportUserFeedback: async ({ userId, eventId, feedback }: UserFeedback) => {
      /**
       * Importing @sentry/browser because it includes the `captureUserFeedback` method
       * which is not available in @sentry/node.
       * This is a workaround until the method is added to @sentry/node.
       * See: https://github.com/getsentry/sentry-javascript/issues/11072
       */
      const { BrowserClient } = await import('@sentry/browser');

      const client = new BrowserClient({
        enabled: sentryClient.getOptions().enabled,
        dsn: sentryClient.getOptions().dsn,
        integrations: [],
        transport: makeNodeTransport,
        stackParser: defaultStackParser,
      });

      client.captureUserFeedback({
        event_id: eventId,
        name: '',
        email: `${userId}@wix.com`,
        comments: outdent`
          ---
          user: https://bo.wix.com/um/users/${userId}
          ---
          ${feedback}
        `,
      });
    },
    setContext: (name: string, context: Record<string, unknown>) =>
      sentryScope.setContext(name, context),
    setUser: (user: User) => sentryScope.setUser(user),
    getUser: (): User | undefined => {
      const user = sentryScope.getUser();
      if (typeof user?.id === 'string') {
        return { id: user.id };
      }
    },
  };
}
