import React from 'react';
import type { ReactElement, FC, ReactNode } from 'react';
import { Alert, Box, InsightIcon, Text } from '@wix/cli-ui-kit';

export const ErrorMessage: FC<{
  message: string | ReactNode;
  explanation?: string | ReactNode;
  hint?: string | ReactElement;
}> = ({ message, explanation, hint }) => (
  <Box flexDirection="column">
    <Alert type="error">{message}</Alert>

    {explanation && <Alert type="warning">{explanation}</Alert>}

    {hint && (
      <Box paddingTop={1} columnGap={1}>
        <InsightIcon />
        {typeof hint === 'string' ? <Text>{hint}</Text> : hint}
      </Box>
    )}
  </Box>
);

export const SystemErrorMessage: FC<{
  message: string | ReactNode;
  explanation?: string | ReactNode;
  hint?: string | ReactElement;
}> = ({ message, explanation, hint }) => (
  <ErrorMessage
    message={message}
    explanation={explanation}
    hint={
      hint ??
      'This is probably a temporary system error and has been logged. Try again shortly.'
    }
  />
);
