import type { ComponentType } from 'react';
import React from 'react';
import { match } from 'variant';
import { Box, Newline, Text, Link } from '@wix/cli-ui-kit';
import { outdent } from 'outdent';
import type { ZodIssue } from 'zod';
import type { CliErrorCodeType } from '@wix/cli-error';
import { ErrorMessage, SystemErrorMessage } from './components/ErrorMessage.js';

export function getErrorComponent(code: CliErrorCodeType): ComponentType {
  return match(code, {
    UnknownFailure: ({ error }) => {
      return () => (
        <>
          <Text skin="error">
            Oh no, seems like the CLI encountered an unexpected error:
          </Text>

          {error instanceof Error ? (
            <Text skin="error">{error.message}</Text>
          ) : (
            <Text>{JSON.stringify(error)}</Text>
          )}
        </>
      );
    },
    FailedToGetDeviceCode: () => {
      return () => (
        <SystemErrorMessage message="An error occured while logging in: Failed to issue a login device code" />
      );
    },
    FailedToGetAuthToken: () => {
      return () => (
        <SystemErrorMessage message="An error occured while logging in: Failed to issue refresh token" />
      );
    },
    FailedToRefreshTokenForSite: () => {
      return () => (
        <SystemErrorMessage message="An error occured while logging in: Failed to issue refresh token for site" />
      );
    },
    FailedToRenewAuthToken: () => {
      return () => (
        <SystemErrorMessage message="Failed to renew access token." />
      );
    },
    FailedToRenewAuthTokenSiteNotFound: ({ siteId }) => {
      return () => (
        <ErrorMessage
          message={`Authenticate failed, your configured development site (${siteId}) could not be accessed`}
          hint="Make sure the site exists and you still have access to it"
        />
      );
    },
    FailedToRenewAuthTokenRefreshTokenRevoked: () => {
      return () => (
        <ErrorMessage
          message="Failed to renew access token: refresh token has been revoked"
          hint="Please login again to get a new refresh token"
        />
      );
    },
    FailedToRevokeRefreshToken: ({ email }) => {
      return () => (
        <SystemErrorMessage
          message="An error occured while trying to logout: Failed to revoke refresh token!"
          explanation={`Wix CLI is still logged in to account ${email}`}
        />
      );
    },
    FailedToFetchPackageJson: () => {
      return () => (
        <SystemErrorMessage message="Failed to fetch package data." />
      );
    },
    FailedToCreateProjectFolder: ({ cause, packageFolder }) => {
      const causeMessage = getErrorMessage(cause);
      return () => (
        <ErrorMessage
          message={`Failed to create project folder: ${packageFolder}. Cause: ${causeMessage}`}
        />
      );
    },
    LatestVersionOfCreateAppRequired: ({
      currentVersion,
      latestVersion,
      updateCommand,
    }) => {
      return () => (
        <ErrorMessage
          message={
            <>
              Generating a Wix App is only supported using the latest version (
              {latestVersion}) of <Text bold>@wix/create-app</Text> (current:{' '}
              {currentVersion})
            </>
          }
          hint={
            <Text>
              Make sure you are using the following command:
              <Newline />
              <Text bold>{updateCommand}</Text>
              <Newline />
              If the problem persists, try clearing your package manager's cache
            </Text>
          }
        />
      );
    },
    EmptyDirectoryRequiredAppGeneration: ({ folder }) => {
      return () => (
        <ErrorMessage
          message={
            <>
              Generating a Wix App requires an empty directory but{' '}
              <Text bold>{folder}</Text> already exists and isn't empty
            </>
          }
          hint={
            <Text>
              Choose a different project name or delete the existing{' '}
              <Text bold>{folder}</Text> folder
            </Text>
          }
        />
      );
    },
    InvalidNewPackageName: ({ packageName, validations }) => {
      return () => (
        <ErrorMessage
          message={
            <>
              Generating a Wix App requires a valid npm package name for the
              generated <Text bold>package.json</Text> <Newline />
              The name <Text bold>{packageName}</Text> failed the following
              validations:
              <Newline />
              {validations.map((validation, id) => (
                <Text key={id}>
                  <Newline />- {validation}
                </Text>
              ))}
            </>
          }
          hint={`Choose a different project name that follows the validations above`}
        />
      );
    },
    SiteSelectorTimedout: () => {
      return () => (
        <ErrorMessage
          message={`Waiting for application installation timed out.`}
        />
      );
    },
    TypeErrorInvalidUrl: () => {
      return () => <ErrorMessage message={`Invalid assets URL.`} />;
    },
    UnsupportedPackageManager: ({ packageManagerName }) => {
      return () => (
        <ErrorMessage
          message={`Generating a Wix App requires the usage of a supported package manager`}
          hint={`Expected Yarn or NPM but ${
            packageManagerName
              ? `got ${packageManagerName}`
              : `failed to detect a package manager`
          }`}
        />
      );
    },
    UnsupportedPackageManagerWorkspaces: ({ packageManagerName }) => {
      return () => (
        <ErrorMessage
          message={`Generating a Wix App inside ${packageManagerName} workspaces is not supported`}
        />
      );
    },
    FailedToGetDeveloperApps: () => {
      return () => (
        <SystemErrorMessage message="Failed to get the list of your applications" />
      );
    },
    FailedToCreateDevCenterApp: () => {
      return () => (
        <SystemErrorMessage message="Failed to register your application in Wix" />
      );
    },
    FailedToCreateTunnel: () => {
      return () => <ErrorMessage message="Failed to create tunnel" />;
    },
    FailedToDeleteTunnel: () => {
      return () => <ErrorMessage message="Failed to delete tunnel" />;
    },
    SitePermissionDenied: ({ siteId }) => {
      return () => {
        return (
          <ErrorMessage
            message={`You don't have permissions to site ${siteId}`}
          />
        );
      };
    },
    AppPermissionDenied: ({ appId }) => {
      return () => {
        return (
          <ErrorMessage
            message={`You don't have permissions to application ${appId}`}
          />
        );
      };
    },
    FailedToUpdateTelemetryConfig: () => {
      return () => (
        <ErrorMessage message="Failed to change telemetry preferences" />
      );
    },
    FailedToInstallPackage: ({ packageName, cause }) => {
      const causeMessage = getErrorMessage(cause);
      return () => (
        <ErrorMessage
          message={`Failed to install npm package ${packageName}. Cause: ${causeMessage}`}
        />
      );
    },
    FailedToResolveNpmDependencies: () => {
      return () => (
        <SystemErrorMessage message="Failed to resolve npm dependencies" />
      );
    },
    FailedToInstallPackages: ({ cause }) => {
      const causeMessage = getErrorMessage(cause);
      return () => (
        <ErrorMessage
          message={`Failed to install npm packages. Cause: ${causeMessage}`}
        />
      );
    },
    FailedMigrationToAnyNpm: () => {
      return () => (
        <ErrorMessage
          message={<>Could not start the migration process</>}
          hint={
            <Text>
              Make sure that your package.json does not have packages installed
              outside of dev dependencies
            </Text>
          }
        />
      );
    },
    DeprecatedVersion: ({ currentVersion }) => {
      return () => (
        <ErrorMessage
          message={
            <>
              {`Your version of the Wix CLI (${currentVersion}) is`}
              <Text bold> deprecated</Text>.
            </>
          }
          hint={`Please update to the latest version to continue`}
        />
      );
    },
    FailedToUninstallPackage: ({ packageName, cause }) => {
      const causeMessage = getErrorMessage(cause);
      return () => (
        <ErrorMessage
          message={`Failed to uninstall npm package ${packageName}. Cause: ${causeMessage}`}
        />
      );
    },
    FailedToSyncToRevision: ({ revision }) => {
      return () => (
        <ErrorMessage
          message={`Failed to sync the local project to revision ${revision}`}
        />
      );
    },
    FailedToCreateVersion: () => {
      return () => (
        <ErrorMessage message="Wix CLI failed to create version for your app" />
      );
    },
    FailedToReadCache: ({ path }) => {
      return () => (
        <ErrorMessage message={`Failed to read cache file at ${path}.`} />
      );
    },
    FailedToWriteCache: ({ path }) => {
      return () => (
        <ErrorMessage message={`Failed to write cache file at ${path}.`} />
      );
    },
    UploadApplicationFailed: () => {
      return () => <ErrorMessage message="Failed to upload the application" />;
    },
    BuiltProjectNeeded: () => {
      return () => (
        <ErrorMessage message="Can not find build output, please execute `wix app build` to build your app before proceeding" />
      );
    },
    WixUninstallMissingPackageName: () => {
      return () => <ErrorMessage message="Please specify a package name" />;
    },
    FailedToReadJson: ({ filePath, cause }) => {
      const causeMessage = getErrorMessage(cause);
      return () => (
        <ErrorMessage
          message={`Failed to read ${filePath}. Cause: ${causeMessage}`}
        />
      );
    },
    InvalidJsonFile: ({ filePath, cause }) => {
      const causeMessage = getErrorMessage(cause);
      return () => (
        <ErrorMessage
          message={`Invalid JSON file located at ${filePath}. Cause: ${causeMessage}`}
        />
      );
    },
    FailedToWriteWixLock: () => {
      return () => <ErrorMessage message={`Failed to write 'wix.lock'`} />;
    },
    InvalidVeloConfigJson: ({ configFilename, cause }) => {
      const causeMessage = getErrorMessage(cause);
      return () => (
        <ErrorMessage
          message={`Invalid ${configFilename}, make sure it's configured correctly. Cause: ${causeMessage}`}
        />
      );
    },
    DashboardPageConfigNotFound: ({ configPath }) => {
      return () => (
        <ErrorMessage
          message={`Dashboard page config not found at ${configPath}`}
        />
      );
    },
    EmbeddedScriptConfigNotFound: ({ configPath }) => {
      return () => (
        <ErrorMessage
          message={`Embedded script config not found at ${configPath}`}
        />
      );
    },
    CustomElementConfigNotFound: ({ configPath }) => {
      return () => (
        <ErrorMessage
          message={`Site widget config not found at ${configPath}`}
        />
      );
    },
    NoExtensionsFound: () => {
      return () => (
        <ErrorMessage
          message="Your project doesn't seem to have any extensions."
          explanation="Extensions are the building blocks of your app."
          hint="Make sure it is configured properly, or read our documentation (https://wix.to/b7o4Ml0) to start developing your first extension."
        />
      );
    },
    NoPagesDirectoryFound: ({ srcDir }) => {
      return () => (
        <ErrorMessage
          message={
            <>
              No <Text bold>pages</Text> directory was found under{' '}
              <Text bold>{srcDir}</Text>.
            </>
          }
          hint={
            <Text>
              Consider re-cloning the project repository or creating a{' '}
              <Text bold>pages</Text> directory under <Text bold>{srcDir}</Text>
              .
            </Text>
          }
        />
      );
    },
    DashboardPluginConfigNotFound: ({ configPath }) => {
      return () => (
        <ErrorMessage
          message={`Dashboard plugin config not found at ${configPath}`}
        />
      );
    },
    DashboardMenuPluginConfigNotFound: ({ configPath }) => {
      return () => (
        <ErrorMessage
          message={`Dashboard menu plugin config not found at ${configPath}`}
        />
      );
    },
    ServicePluginConfigNotFound: ({ configPath }) => {
      return () => (
        <ErrorMessage
          message={`Service plugin config not found at ${configPath}`}
        />
      );
    },
    DashboardModalConfigNotFound: ({ configPath }) => {
      return () => (
        <ErrorMessage
          message={`Dashboard modal config not found at ${configPath}`}
        />
      );
    },
    InvalidConfigSchemaError: ({ configPath, zodError }) => {
      return () => (
        <ErrorMessage
          message={outdent`
            Invalid configuration found at \`${configPath}\`:
            The following errors were found:
            ${zodError.errors.map(formatZodIssue).join('\n')}`}
          hint="Fix the errors above for your configurations to be applied successfully."
        />
      );
    },
    InvalidParamsOverrideSchemaError: ({ paramsOverridePath, zodError }) => {
      return () => (
        <ErrorMessage
          message={outdent`
            Invalid params override found at \`${paramsOverridePath}\`:
            The following errors were found:
            ${zodError.errors.map(formatZodIssue).join('\n')}`}
          hint="Fix the errors above for your params override to be applied successfully."
        />
      );
    },
    InvalidDependenciesJsonSchemaError: ({ path, zodError }) => {
      return () => (
        <ErrorMessage
          message={outdent`
            Invalid configuration found at \`${path}\`:
            The following errors were found:
            ${zodError.errors.map(formatZodIssue).join('\n')}`}
          hint={
            <Text>
              Try to re-install <Text bold>@wix/cli-app</Text> dependency
            </Text>
          }
        />
      );
    },
    PermissionDenied: () => {
      return () => (
        <ErrorMessage message="Forbidden operation. Missing permissions" />
      );
    },
    FailedToReadProjectFiles: ({ cause }) => {
      const causeMessage = getErrorMessage(cause);
      return () => (
        <ErrorMessage
          message={`Failed to read source files: make sure you have an 'src' folder and that it's readable. Cause: ${causeMessage}`}
        />
      );
    },
    FailedToCreateVersionForSubmittedApp: () => {
      return () => (
        <ErrorMessage
          message="Cannot create a new application version because the app has an active marketplace submission"
          hint="You will be able continue creating versions when your submission is approved or retracted"
        />
      );
    },
    GridAppFilesMaxLengthExcceeded: ({ maxSizeViolationsData, filePaths }) => {
      let errorMessage =
        'Found one or more project files that are bigger than the allowed limit';

      if (maxSizeViolationsData?.files.length) {
        errorMessage += ` (${
          maxSizeViolationsData.expectedSize
        }KB): ${maxSizeViolationsData.files
          .map((f) => `${filePaths[f.fileIndex]} (${f.receivedSize}KB)`)
          .join(', ')}`;
      }

      return () => <ErrorMessage message={errorMessage} />;
    },
    GridAppFailedToUpdateFiles: () => {
      return () => (
        <ErrorMessage
          message={outdent`
            Failed to update files.
            Failed to sync changes from local files to the Dev Editor
            Still watching local files and will continue to sync changes...
          `}
        />
      );
    },
    FailedToWatchFiles: ({ cause }) => {
      const causeMessage = getErrorMessage(cause);

      return () => (
        <ErrorMessage
          message={`Failed to watch files. Cause: ${causeMessage}`}
        />
      );
    },
    FailedToCreateGridApp: ({ error }) => {
      return () => (
        <ErrorMessage
          message="Failed to create an isolated environment."
          explanation={error}
        />
      );
    },
    DeviceCodeTimedOut: () => {
      return () => (
        <ErrorMessage message="The device code has not been approved in the time allocated, please start the login process again." />
      );
    },
    DuplicateComponentIdsError: ({ issues }) => {
      const Issue = ({ issue }: { issue: (typeof issues)[0] }) => {
        const typeToName = (type: (typeof issue)['components'][0]['type']) =>
          match(type, {
            page: () => 'page',
            plugin: () => 'plugin',
            'menu-plugin': () => 'menu plugin',
            'sidebar-category': () => 'sidebar category',
            'embedded-script': () => 'embedded script',
            'custom-element': () => 'custom element',
            modal: () => 'modal',
            topology: () => 'topology',
          });
        return (
          <Box flexDirection="column">
            <Text>
              <Text bold>Duplicate ID: </Text>
              {issue.id}
            </Text>
            <Text bold>Extensions:</Text>
            {issue.components.map(({ name, path, type }) => (
              <Text key={path}>
                [{typeToName(type)}] {name} ({path})
              </Text>
            ))}
            <Text>
              <Text bold>Possible GUID: </Text>
              {issue.suggestedId}
            </Text>
          </Box>
        );
      };
      const hint = (
        <Box flexDirection="column" rowGap={1}>
          <Text>
            You can use the provided suggested GUIDs as the ID of the newly
            created extension.
          </Text>
          {issues.map((issue) => (
            <Issue issue={issue} key={issue.id} />
          ))}
        </Box>
      );

      return () => (
        <ErrorMessage
          message="Found multiple extensions with the same id, this might happen if you copy paste folders."
          hint={hint}
        />
      );
    },
    InvalidResponseData: () => {
      return () => (
        <SystemErrorMessage message="Got unexpected response from server." />
      );
    },
    InvalidCreateAppResponse: () => {
      return () => (
        <ErrorMessage message="`createApp` returned an invalid response." />
      );
    },
    InvalidDeployAppResponse: () => {
      return () => (
        <ErrorMessage message="Got unexpected response from server. Make sure you are running a supported version of the Velo CLI." />
      );
    },
    InvalidGetDeploymentResponse: () => {
      return () => (
        <ErrorMessage message="Got unexpected response from server. Please try again later.." />
      );
    },
    FailedToGetPagesDiff: () => {
      return () => <ErrorMessage message="Failed to request pages diff." />;
    },
    InvalidViteBuildOutput: () => {
      return () => <ErrorMessage message="Invalid vite build output" />;
    },
    ViteModuleBundleNotFound: () => {
      return () => (
        <ErrorMessage message="Could not find module bundle in vite build output" />
      );
    },
    ViteFileNotFoundInOutput: ({ fileName }) => {
      return () => (
        <ErrorMessage
          message={`Could not find ${fileName} in vite build output`}
        />
      );
    },
    FailedToCreateBackendDeployment: () => {
      return () => (
        <ErrorMessage message="Failed to create backend deployment" />
      );
    },
    FailedToUpdateBackendDeployment: () => {
      return () => (
        <ErrorMessage message="Failed to update backend deployment" />
      );
    },
    InvalidDeploymentUrl: ({ deploymentUrl }) => {
      return () => (
        <ErrorMessage
          message={`Invalid deployment URL: \`${deploymentUrl}\``}
        />
      );
    },
    FailedToGetBackendDeployment: () => {
      return () => <ErrorMessage message="Failed to get backend deployment" />;
    },
    FailedToDeployBackend: () => {
      return () => <ErrorMessage message="Failed to deploy backend" />;
    },
    FailedToMarkDeploymentAsPermanent: () => {
      return () => (
        <ErrorMessage message="Failed to make backend deployment permanent" />
      );
    },
    FailedToGetUserInfo: () => {
      return () => <ErrorMessage message="Failed to get user information." />;
    },
    FailedToGetMyAccount: () => {
      return () => (
        <SystemErrorMessage message="Failed to get account information." />
      );
    },
    FailedToCreateDevelopmentSite: () => {
      return () => (
        <ErrorMessage message="Failed to create a Development Site" />
      );
    },
    FailedToGetDevelopmentSites: () => {
      return () => (
        <ErrorMessage message="Failed to request development sites." />
      );
    },
    FailedToGetDevelopmentSitesLimit: () => {
      return () => (
        <ErrorMessage message="Failed to request development sites limit." />
      );
    },
    FailedToGetRequiredApps: () => {
      return () => <ErrorMessage message="Failed to get app required apps." />;
    },
    FailedToGetMarketListing: () => {
      return () => <ErrorMessage message="Failed to get market listing." />;
    },
    FailedToAddRequiredApp: () => {
      return () => <ErrorMessage message="Failed to add required app." />;
    },
    FailedToQuerySites: () => {
      return () => <ErrorMessage message="Failed to request sites list." />;
    },
    FailedToGetSite: () => {
      return () => (
        <ErrorMessage message="Failed to get information about your configured Development Site" />
      );
    },
    FailedToDeploySite: () => {
      return () => <ErrorMessage message="Failed to deploy site." />;
    },
    FailedToPublishDeployment: () => {
      return () => <SystemErrorMessage message="Failed to publish site." />;
    },
    FailedToSaveDevelopmentSite: ({ path }) => {
      return () => (
        <ErrorMessage
          message="Failed to save the current development site."
          hint={`Make sure the \`${path}\` file has proper permissions and is accessible`}
        />
      );
    },
    FailedToBuildApp: ({ cause }) => {
      const causeMessage = getErrorMessage(cause);
      return () => (
        <ErrorMessage
          message={
            <>
              Failed to build the application.
              <Newline />
              {causeMessage}
            </>
          }
          hint={'Please check the build output for more information'}
        />
      );
    },
    FailedToCheckIfAppInstalled: () => {
      return () => (
        <ErrorMessage message="Failed to fetch site installation details" />
      );
    },
    FailedToInitializeGitRepo: ({ cause }) => {
      const causeMessage = getErrorMessage(cause);
      return () => (
        <ErrorMessage
          message={`Failed to initialize git repo. Cause: ${causeMessage}`}
        />
      );
    },
    FailedToInstallDependencies: ({ cause }) => {
      const causeMessage = getErrorMessage(cause);
      return () => (
        <ErrorMessage
          message={`Failed to install dependencies. Cause: ${causeMessage}`}
        />
      );
    },
    FailedToCommitChanges: ({ cause }) => {
      const causeMessage = getErrorMessage(cause);
      return () => (
        <ErrorMessage
          message={`Failed to commit changes. Cause: ${causeMessage}`}
        />
      );
    },
    FailedToDeleteAuthData: ({ cause }) => {
      const causeMessage = getErrorMessage(cause);
      return () => (
        <ErrorMessage message={`Failed to delete auth data: ${causeMessage}`} />
      );
    },
    FailedToDeleteLegacyAuthData: ({ cause }) => {
      const causeMessage = getErrorMessage(cause);
      return () => (
        <ErrorMessage
          message={`Failed to delete legacy auth data: ${causeMessage}`}
        />
      );
    },
    FailedToSaveAuthData: ({ cause }) => {
      const causeMessage = getErrorMessage(cause);
      return () => (
        <ErrorMessage message={`Failed to save auth data: ${causeMessage}`} />
      );
    },
    FailedToSaveApiKeyAuthData: ({ cause }) => {
      const causeMessage = getErrorMessage(cause);
      return () => (
        <ErrorMessage
          message={`Failed to save api key auth data: ${causeMessage}`}
        />
      );
    },
    FailedToWriteJson: ({ filePath, cause }) => {
      const causeMessage = getErrorMessage(cause);
      return () => (
        <ErrorMessage
          message={`Failed to write JSON at path: ${filePath}. Cause: ${causeMessage}`}
        />
      );
    },
    TemplatePathNotFound: ({ templatePath }) => {
      return () => (
        <ErrorMessage
          message={`Template path not found: ${templatePath}`}
          hint="Make sure the template path exists in the remote repository"
        />
      );
    },
    FailedToCloneRepo: ({ cause }) => {
      const causeMessage = getErrorMessage(cause);
      return () => (
        <ErrorMessage
          message={`Failed to clone repo: ${causeMessage}`}
          hint="Make sure the Git URL is correct and that your local Git is configured properly to access the remote repository"
        />
      );
    },
    InvalidGitUrl: ({ url }) => {
      return () => (
        <ErrorMessage
          message={`Invalid Git URL: ${url}`}
          hint="Make sure the Git URL is in the correct format"
        />
      );
    },
    AuthenticationRequired: () => {
      return () => (
        <ErrorMessage message="You have to already be logged in to run this command." />
      );
    },
    SiteConfiguredRequired: () => {
      return () => (
        <ErrorMessage message="You must configure the development site before running this command, or provide a site-id in the command options." />
      );
    },
    FailedAddingPackageToWorkspace: () => {
      return () => (
        <ErrorMessage
          message="Could not add package to the workspaces configuration. Please add it as instructed by your package manager."
          hint={
            <Text>
              Full error can be found in <Text bold>wix-error.log</Text>
            </Text>
          }
        />
      );
    },
    FailedToGenerateSelfsignedCertificate: () => {
      return () => (
        <ErrorMessage message="Failed to generate a Selfsigned certificate for development." />
      );
    },
    NonInteractiveTerminal: () => {
      return () => (
        <ErrorMessage message="This command is not supported in non-interactive terminals. Please run the command in an interactive terminal (TTY)." />
      );
    },
    FailedToResolveViteOrigin: () => {
      return () => (
        <ErrorMessage message="Failed to resolve the base url for CSS asset urls" />
      );
    },
    FailedToCreateAppRelease: () => {
      return () => (
        <SystemErrorMessage message="Failed to create an app configuration." />
      );
    },
    FailedToUpdateAppRelease: () => {
      return () => (
        <SystemErrorMessage message="Failed to update an app configuration." />
      );
    },
    FailedToCreateComponents: () => {
      return () => (
        <SystemErrorMessage message="Failed to create components." />
      );
    },
    FailedToCheckDeprecatedStatus: () => {
      return () => (
        <SystemErrorMessage message="Failed to check cli version deprecated status." />
      );
    },
    FailedToGetResolveNpmDependenciesResult: () => {
      return () => (
        <SystemErrorMessage message="Failed to resolve Wix's lockfile." />
      );
    },
    CliAppVersionMismatch: ({ cliVersion, cliAppVersion }) => {
      return () => (
        <ErrorMessage
          message={
            <Text>
              The versions of <Text bold>@wix/cli</Text> ({cliVersion}) and{' '}
              <Text bold>@wix/cli-app</Text> ({cliAppVersion}) dependencies do
              not match
            </Text>
          }
          hint={
            <Text>
              Align their versions in your <Text bold>package.json</Text> and
              re-install dependencies
            </Text>
          }
        />
      );
    },
    FailedToGetAppValidity: () => {
      return () => (
        <SystemErrorMessage message="Failed to get validation status for app version" />
      );
    },
    InvalidDashboardPageRoute: ({ route, errorMessage }) => {
      return () => (
        <ErrorMessage
          message={`Invalid dashboard page route: ${route}`}
          hint={errorMessage}
        />
      );
    },
    InvalidEmbeddedScriptFolder: ({ path, errorMessage }) => {
      return () => (
        <ErrorMessage
          message={`Invalid embedded script folder: ${path}`}
          hint={errorMessage}
        />
      );
    },
    InvalidEmbeddedScriptTemplate: ({ templateFilename, errorMessage }) => {
      return () => (
        <ErrorMessage
          message={`Invalid embedded script template in ${templateFilename}`}
          hint={errorMessage}
        />
      );
    },
    InvalidCustomElementTagName: ({ tagName, errorMessage }) => {
      return () => (
        <ErrorMessage
          message={
            <Text>
              We use name of folder as tagName for your site widget.
              Unfortunately, <Text bold>{tagName}</Text> is invalid tagName.
              Reason: {errorMessage}
            </Text>
          }
          hint={
            <Text>
              You can find rules for tagName here:{' '}
              <Text bold>
                https://developer.mozilla.org/en-US/docs/Web/API/CustomElementRegistry/define#valid_custom_element_names
              </Text>
            </Text>
          }
        />
      );
    },
    InvalidSiteFrontendPackageVersion: ({ packageName, versionRange }) => {
      return () => (
        <ErrorMessage
          message={
            <Text>
              Package <Text bold>{packageName}</Text> has an invalid version
              range <Text bold>{versionRange}</Text> in your{' '}
              <Text bold>package.json</Text> file.
            </Text>
          }
          hint={
            <Text>
              The version range must follow{' '}
              <Text bold>semantic versioning</Text> rules, and it must contain a
              major version.
              <Newline />
              Read more about it here:{' '}
              <Link url="https://docs.npmjs.com/about-semantic-versioning" />
            </Text>
          }
        />
      );
    },
    FailedToCreateSiteOverride: () => {
      return () => (
        <SystemErrorMessage message="Failed to create site override configuration" />
      );
    },
    FailedToGetPipelineErrorInfo: () => {
      return () => <SystemErrorMessage message="Failed to get error details" />;
    },
    FailedToGetDeploymentStatus: () => {
      return () => (
        <SystemErrorMessage message="Failed to get deployment status" />
      );
    },
    NetworkError: ({ cause }) => {
      const causeMessage = getErrorMessage(cause);
      return () => (
        <ErrorMessage
          message={`Network error occurred. Cause: ${causeMessage}`}
          hint={`Make sure you have an active internet connection and try again shortly.`}
        />
      );
    },
    FailedToGenerateAppMarketQueryParam: () => {
      return () => (
        <SystemErrorMessage message="Failed to get generate appMarket query parameter" />
      );
    },
    FailedToParseLog: () => {
      return () => (
        <SystemErrorMessage message="Failed to parse log from the server" />
      );
    },
    AppForVersionNotFound: ({ version }) => {
      return () => (
        <ErrorMessage
          message={`Could not find app configuration for the provided version: "${version}"`}
          hint={`Make sure that there is an app for this version.`}
        />
      );
    },
    FailedToFetchAppForVersion: () => {
      return () => (
        <SystemErrorMessage message={`Failed to fetch app configuration`} />
      );
    },
    FailedToGetAppSecret: () => {
      return () => (
        <SystemErrorMessage
          message={`Failed to fetch app keys and configuration`}
        />
      );
    },
    FailedToConnectToLogsService: () => {
      return () => (
        <ErrorMessage message={`Failed to connect to logs service.`} />
      );
    },
    FailedToGetLogsTopology: () => {
      return () => (
        <ErrorMessage
          message={`Failed to fetch logs configuration for the given version.`}
          hint={`Try to use a different version.`}
        />
      );
    },
    FailedInstallCloudflareNoAccess: ({ path }) => {
      return () => (
        <ErrorMessage
          message={`Failed to install required binary in "${path}"`}
          hint={`Check that you have required permissions.`}
        />
      );
    },
    LogsConnectionTimeout: () => {
      return () => (
        <ErrorMessage message={`Log session has been closed due to timeout.`} />
      );
    },
    InvalidServicePluginImplementation: () => {
      return () => (
        <ErrorMessage
          message={`Your service plugin implementation is incorrect.`}
        />
      );
    },
    UnknownServicePlugin: ({ name }) => {
      return () => (
        <ErrorMessage
          message={`Unknown service plugin with the name of \`${name}\``}
        />
      );
    },
    InvalidServicePluginFolder: ({ relativePath }) => {
      return () => (
        <ErrorMessage
          message={`Invalid folder for service plugin in \`${relativePath}\``}
        />
      );
    },
    InvalidSiteOverrideID: () => {
      return () => (
        <ErrorMessage
          message={`Failed to read devSiteOverrideId from local app configurations.`}
          hint={`Restart the dev environment and make sure that the .wix/app.config.json file exists.`}
        />
      );
    },
  });
}

function getErrorMessage(cause: unknown) {
  return cause instanceof Error ? cause.message : JSON.stringify(cause);
}

function formatZodIssue(issue: ZodIssue) {
  const path = issue.path.join('.');

  if (!path) {
    return `- ${issue.message}`;
  }

  return `- \`${path}\`: ${issue.message}`;
}
