import process from 'node:process';
import React from 'react';
import { renderErrorSync } from '@wix/cli-ui-kit';
import { writeCrashReport } from '@wix/cli-debug-log';
import { ErrorViewer } from './components/ErrorViewer.js';
import type { ErrorReporterOptions } from './error-reporter.js';
import { createErrorReporter } from './error-reporter.js';
import { collectUserFeedback } from './user-feedback.js';

export type ErrorHandlerFn = (args: { error: unknown; data?: unknown }) => void;

export function createErrorHandler({
  debugOutputPath,
  userFeedbackCachePath,
  ...errorReporterOptions
}: {
  debugOutputPath: string;
  userFeedbackCachePath?: string;
} & ErrorReporterOptions) {
  const errorReporter = createErrorReporter(errorReporterOptions);

  const handler: ErrorHandlerFn = function ({ error, data }) {
    const reportResult = errorReporter.reportError(error, { data });
    renderErrorSync(<ErrorViewer error={error} />);
    writeCrashReport(debugOutputPath, error);
    process.exitCode = 1;

    collectUserFeedback({
      reportResult,
      errorReporter,
      cachePath: userFeedbackCachePath,
    }).catch((e: unknown) => {
      writeCrashReport(debugOutputPath, e);
    });
  };

  const registerToProcessErrors = function () {
    process.on('uncaughtException', (error) => {
      handler({
        error,
        data: { mechanism: { handled: false, type: 'uncaughtException' } },
      });
    });

    process.on('unhandledRejection', (error) => {
      /**
       * Temporary workaround for https://github.com/vitejs/vite/issues/14706
       */
      if (
        error instanceof Error &&
        error.message.startsWith('Build failed with') &&
        error.stack?.includes('esbuild')
      ) {
        return;
      }

      handler({
        error,
        data: { mechanism: { handled: false, type: 'unhandledRejection' } },
      });
    });
  };

  return {
    handler,
    errorReporter,
    registerToProcessErrors,
  };
}
